.menu {
  padding-top: 50px; }
  @media (max-width: 990px) {
    .menu {
      padding-top: 20px; } }
  .menu a {
    font-size: 17px;
    font-weight: 600;
    display: inline-block;
    padding: 2px 8px 4px;
    margin-right: 30px;
    text-decoration: none;
    color: #241e1e;
    margin-bottom: 35px;
    transition: all 100ms ease-in-out; }
    .menu a:not(.selected):hover {
      background-color: #ccdeba;
      color: #689229;
      box-shadow: 6px 6px #689229; }
  .menu .selected {
    background-color: #689229;
    color: white;
    box-shadow: 6px 6px #ccdeba; }



