.rightContainer {
  width: calc(50% - 35px);
  height: 100%;
  right: 0;
  top: 0;
  position: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  /*Hide on small screeen*/ }
  @media (max-width: 990px) {
    .rightContainer {
      display: none; } }
  .rightContainer .github {
    opacity: 0.25;
    position: absolute;
    bottom: 15px;
    right: 15px;
    text-decoration: none;
    color: white;
    font-weight: bold;
    font-size: 15px;
    transition: 200ms all ease-out; }
    .rightContainer .github img {
      height: 20px;
      margin-bottom: -4px;
      padding-right: 5px; }
    .rightContainer .github:hover {
      opacity: 0.9; }



