.wrapper .name {
  display: block;
  position: relative;
  z-index: 0;
  line-height: 1.1em;
  margin-bottom: 5px; }
  .wrapper .name::after {
    display: block;
    content: "";
    width: 101%;
    height: 20%;
    background: #ccdeba;
    position: absolute;
    bottom: 0px;
    left: 0;
    z-index: -1;
    transition: all 100ms ease-in-out;
    pointer-events: none;
    transform: translate(0px, 5px); }

.wrapper .position {
  font-size: 1.5em;
  font-weight: 700;
  padding-top: 5px;
  color: #689229; }



